import { Component, inject, Output, EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { AuthService } from '../../services/auth.service';
import { FirestoreService } from '../../services/firestore.service';
import { SubdomainService } from '../../services/subdomain.service';

@Component({
  selector: 'app-create-board-modal',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './create-board-modal.component.html',
  styleUrls: ['./create-board-modal.component.scss']
})
export class CreateBoardModalComponent {
  private authService = inject(AuthService);
  private firestoreService = inject(FirestoreService);
  private subdomainService = inject(SubdomainService);

  @Output() boardCreated = new EventEmitter<void>();
  @Output() closeModal = new EventEmitter<void>();

  boardName = '';
  boardDescription = '';
  isLoading = false;
  errorMessage = '';
  isVisible = false;

  show() {
    this.isVisible = true;
    this.resetForm();
  }

  hide() {
    this.isVisible = false;
    this.closeModal.emit();
  }

  resetForm() {
    this.boardName = '';
    this.boardDescription = '';
    this.errorMessage = '';
    this.isLoading = false;
  }

  async createBoard() {
    if (!this.boardName.trim()) {
      this.errorMessage = 'Nome do quadro √© obrigat√≥rio';
      return;
    }

    const currentUser = this.authService.getCurrentUser();
    if (!currentUser) {
      this.errorMessage = 'Usu√°rio n√£o autenticado';
      return;
    }

    this.isLoading = true;
    this.errorMessage = '';

    try {
      // Criar o quadro
      const boardData = {
        name: this.boardName.trim(),
        description: this.boardDescription.trim(),
        companyId: '', // Ser√° preenchido pelo FirestoreService
        createdAt: null // ser√° preenchido pelo serverTimestamp
      };

      const boardRef = await this.firestoreService.createBoard(currentUser.uid, boardData);
      
      // Atualizar com email do usu√°rio ap√≥s cria√ß√£o
      await this.firestoreService.updateBoard(currentUser.uid, boardRef.id, {
        ownerEmail: currentUser.email || ''
      });

      // Emitir evento de sucesso
      this.boardCreated.emit();
      this.hide();
      
    } catch (error: any) {
      console.error('Erro ao criar quadro:', error);
      this.errorMessage = 'Erro ao criar quadro. Tente novamente.';
    } finally {
      this.isLoading = false;
    }
  }

  onBackdropClick(event: MouseEvent) {
    if (event.target === event.currentTarget) {
      this.hide();
    }
  }

  getPrimaryColor(): string {
    const company = this.subdomainService.getCurrentCompany();
    return company?.primaryColor || company?.brandingConfig?.primaryColor || '#3B82F6';
  }
}